import React,{useState} from 'react';
import './App.css';
import {BrowserRouter, Link, Route} from 'react-router-dom';
import './aboutme'
import Aboutme from './aboutme';
import Home from './home.js'
import Resume from './resume'
import Projects from './projects'
function App() {
  return (
   <BrowserRouter>
   <Route exact path={'/endri-seferi'} component={Home}/>
   <Route path='/endri-seferi/aboutme' component={Aboutme}/>
   <Route path='/endri-seferi/resume' component={Resume}/>
   <Route path='/endri-seferi/projects' component={Projects}/>
   </BrowserRouter>
  );
}

export default App;
